<template>
    <div>
        <crud-resource
            :Resource = "capitalMarketPlayers"
            :DeleteResource = "deleteCapitalMarketPlayer"
            :CreateResource = "createCapitalMarketPlayer"
            :RefreshResource = "refreshCapitalMarketPlayers">
        </crud-resource>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

import CrudResource from "../shared/CrudResource";
import * as CapitalMarketPlayerService from "../../services/CapitalMarketPlayers";
import * as MutationService from "../../services/MutationServices";

export default {
  components: {
    CrudResource
  },

  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      capitalMarketPlayers: "capitalMarketPlayers"
    })
  },

  methods: {
    deleteCapitalMarketPlayer: function(capitalMarketPlayer) {
      return CapitalMarketPlayerService.DeleteCapitalMarketPlayer(
        capitalMarketPlayer
      );
    },

    createCapitalMarketPlayer: function(capitalMarketPlayer) {
      return CapitalMarketPlayerService.CreateCapitalMarketPlayer(
        capitalMarketPlayer
      );
    },

    refreshCapitalMarketPlayers: function() {
      MutationService.addCapitalMarketPlayers();
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



