<template>
  <v-container fluid class="height-400pxx" justify-center>
    <v-layout row>

        <!-- FORM INPUTS -->
        <v-flex xs12 sm10 lg6>
          <v-layout row justify-center class="d-flex">

            <!-- Type of Party -->
            <v-flex xs12 class="height-55px mb6">
              <v-select class="" :label="'TYPE OF PARTY'" :items="participantTypes" 
              v-model="participantType">
              </v-select>
            </v-flex>

            <!-- Name Of Party -->
            <v-flex xs12 class="height-55px mb6">
              <v-select class="" :label="'NAME OF PARTY'" :items="capitalMarketPlayers" 
              v-model="capitalMarketPlayer">
              </v-select>
            </v-flex>

            <!-- Signature -->
            <v-flex xs12 class="pl20 pr20 text-center">
                  <file-input
                    accept="image/*"
                    ref="fileInput"
                  @input="getUploadedFile"
              ></file-input>
            </v-flex>

            <!-- Add participant button -->
            <v-flex xs12 text-center class="mt10 mb50">
                <v-btn primary block @click="addNewParticipantParty()">ADD PARTY</v-btn>
            </v-flex>

          </v-layout>
        </v-flex>

    </v-layout>

       <!-- Buttons -->
    <v-layout row>
      <v-btn error @click="goBack()">Previous</v-btn>
      <v-btn primary @click="moveToSelectParties()">Next</v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import _ from "lodash";
import * as mutationTypes from "../../../store/mutation-types";
import { mapGetters } from "vuex";

import FileInput from "../../shared/FileUploader";

export default {
  components: {
    FileInput
  },
  data() {
    return {
      requiredRule: [v => !!v || "Required"],
      // _participantTypes: _.cloneDeep(
      //   this.$store.state.eipo_registrar.new_ipo.participant_types
      // ),
      // participants: _.cloneDeep(
      //   this.$store.state.eipo_registrar.new_ipo.participants
      // ),
      participants: [],
      participantType: null,
      capitalMarketPlayer: null,
      logoFile: null,
      image: "",
      base64Image: "",
      title: "",
      description: ""
    };
  },

  watch: {
    participants: {
      handler: _.debounce(function(participants) {
        this.$store.commit(
          mutationTypes.UPDATE_NEW_IPO_PARTICIPANTS,
          participants
        );
      }, 0),
      deep: true
    }
  },

  computed: {
    ...mapGetters({
      participantTypes: "participantTypes",
      capitalMarketPlayers: "formattedCapitalMarketPlayers"
    })
  },

  methods: {
    addNewParticipantParty() {
      const newIpoParticipant = {
        type: this.participantType,
        name: this.capitalMarketPlayer,
        logo: this.base64Image
      };

      // Add the new participant to the _participants array which is watched, and updated in the store
      this.participants.push(newIpoParticipant);

      alert("New Participant has been added");
      this.participantType = "";
      this.capitalMarketPlayer = "";
      // this.base64Image = null;
      // document.querySelector("#removeBtnForImageFile").click();
    },

    getUploadedFile(e) {
      const img = e;

      let reader = new FileReader();
      reader.addEventListener(
        "load",
        () => {
          this.image = reader.result;
          this.base64Image = reader.result;
        },
        false
      );

      if (img) {
        reader.readAsDataURL(img);
      }
    },

    goBack() {
      this.$store.commit(mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_BACKWARD);
    },

    moveToSelectParties() {
      this.$store.commit(mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_FORWARD);
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



