/**
 * This service file handles calling of services, and making store mutations with their responses.
 * I honestly don't know where best to put these kinda actions, so this is a stop gap solution begging
 * for refactoring from a smarter mind, or future me :)
 *
 */

import * as IpoService from "./IpoService";
import * as mutationTypes from "../store/mutation-types";
import store from "../store";

/**
 * Get the capital market operators from the API, and commit it to the store
 */
export const addCapitalMarketPlayers = () => {
  IpoService.GetCapitalMarketPlayers().then(capitalMarketPlayers => {
    let players = capitalMarketPlayers.data.data
      ? capitalMarketPlayers.data.data
      : [];

    store.commit(mutationTypes.STORE_CAPITAL_MARKET_PLAYERS, players);
  });
};

/**
 * Get the default ipo participant types from the API, and commit it to the store
 */
export const addDefaultPartcipantTypes = () => {
  IpoService.GetParticipantPartyTypes().then(participantTypes => {
    let partyTypes = participantTypes.data.data
      ? participantTypes.data.data
      : [];

    // Transform partyType data structure
    partyTypes = partyTypes.map(partyType => {
      return {
        text: partyType.name,
        value: partyType.id
      };
    });

    store.commit(mutationTypes.STORE_DEFAULT_PARTICIPANT_TYPES, partyTypes);
  });
};

/**
 * Commit all ipos to the store
 * @param ipos
 */
export const addIposToStore = ipos => {
  store.commit(mutationTypes.STORE_IPOS, ipos);
};



