<template>
  <v-container fluid>
      <!-- MultiStep form used in the IPO allocation process -->
      <v-stepper v-model="ipoCreationFormStepper">

        <!-- IPO CREATION FORM headers -->
        <v-stepper-header>
            <v-stepper-step step="1" :complete="ipoCreationFormStepper > 1">IPO Data</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="2" :complete="ipoCreationFormStepper > 2">Offer Participant Setup</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3" :complete="ipoCreationFormStepper > 3">Select Parties</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="4" :complete="ipoCreationFormStepper > 4">Review Data</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="5" >Approval</v-stepper-step>
        </v-stepper-header> 
        <!-- EOF IPO CREATION FORM headers -->

        <!-- Input the IPO creation data -->
        <v-stepper-content step="1">
          <ipo-data></ipo-data>
        </v-stepper-content>
        <!-- EOF IPO creation data -->

        <!-- Offer participant setup -->
        <v-stepper-content step="2">
          <offer-participant-setup></offer-participant-setup>
        </v-stepper-content>
        <!-- EOF Offer participant setup -->

        <!-- Select parties -->
         <v-stepper-content step="3">
          <select-parties></select-parties>
        </v-stepper-content>
        <!-- EOF Select parties -->

        <!-- Review data -->
        <v-stepper-content step="4">
          <review-data></review-data>
        </v-stepper-content>
        <!-- EOF Review data -->

        <!-- IPO Approval -->
        <v-stepper-content step="5">
          <ipo-approval></ipo-approval>
        </v-stepper-content>
        <!-- EOF IPO approval -->

      </v-stepper>
      <!-- EOF MultiStep form used in creating IPO's -->

  </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import * as mutationTypes from "../../store/mutation-types";

import IpoData from "./CreateIPO-forms/IpoData";
import OfferParticipantSetup from "./CreateIPO-forms/OfferParticipantSetup";
import SelectParties from "./CreateIPO-forms/SelectParties";
import ReviewData from "./CreateIPO-forms/ReviewData";
import IpoApproval from "./CreateIPO-forms/IpoApproval";

export default {
  components: {
    IpoData,
    OfferParticipantSetup,
    SelectParties,
    ReviewData,
    IpoApproval
  },

  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      _ipoCreationFormStepper: "ipoCreationFormStepper"
    }),

    ipoCreationFormStepper: {
      get() {
        return this._ipoCreationFormStepper;
      },
      set(stepperIndex) {
        this.$store.commit(
          mutationTypes.UPDATE_IPO_CREATION_FORM_STEPPER,
          stepperIndex
        );
      }
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



