/**
 * This module encapsulates generic functions which do not necessarily have a module to belong to.
 */
export const formatParticipationTypes = partyTypes => {
  let formattedPartyTypes = [];
  partyTypes.forEach(partyType => {
    formattedPartyTypes.push({
      text: partyType,
      value: partyType
    });
  });

  return formattedPartyTypes;
};



