import * as mutationTypes from "../../../mutation-types";
import { IParticipant } from "../../../../models/ipo.interface";

const mutations = {
  [mutationTypes.UPDATE_IPO_CREATION_STATE](state, newIpoState) {
    state.new_ipo = {
      ...state.new_ipo,
      ...newIpoState
    };
  },

  /**
   * Update the new_ipo data in the store
   *
   * @param {any} state
   * @param {any} newIpoState
   */
  [mutationTypes.UPDATE_NEW_IPO_STATE](state, newIpoState) {
    state.new_ipo = {
      ...state.new_ipo,
      ...newIpoState
    };
  },

  /**
   * Update the modify_ipo data in the store
   *
   * @param {any} state
   * @param {any} newIpoState
   */
  [mutationTypes.UPDATE_MODIFY_IPO_STATE](state, newIpoState) {
    state.modify_ipo = {
      ...state.modify_ipo,
      ...newIpoState
    };
  },

  /**
   * Update the participants data for new ipo
   *
   * @param {any} state
   * @param {any} participants
   */
  [mutationTypes.UPDATE_NEW_IPO_PARTICIPANTS](state, participants) {
    state.new_ipo.participants = participants;
  },

  /**
   * Update the participants data for modify ipo
   *
   * @param {any} state
   * @param {any} participants
   */
  [mutationTypes.UPDATE_MODIFY_IPO_PARTICIPANTS](state, participants) {
    state.modify_ipo.participants = participants;
  },

  /**
   * Update the participant types that can be assigned to the various participants in an IPO.
   *
   * @param {any} state
   * @param {any} newParticipantTypes
   */
  [mutationTypes.UPDATE_NEW_IPO_PARTICIPANT_TYPES](state, newParticipantTypes) {
    state.new_ipo.participant_types = newParticipantTypes;
  },

  /**
   * Update the participant types that can be assigned to the various participants in an IPO.
   *
   * @param {any} state
   * @param {any} newParticipantTypes
   */
  [mutationTypes.UPDATE_MODIFY_IPO_PARTICIPANT_TYPES](
    state,
    newParticipantTypes
  ) {
    state.modify_ipo.participant_types = newParticipantTypes;
  },

  /**
   * Move the create ipo form stepper one step backward.
   *
   * @param {any} state
   */
  [mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_BACKWARD](state) {
    state.ipoCreationFormStepper = state.ipoCreationFormStepper - 1;
  },

  /**
   * Move the create ipo form stepper one step forward.
   *
   * @param {any} state
   */
  [mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_FORWARD](state) {
    state.ipoCreationFormStepper = state.ipoCreationFormStepper + 1;
  },

  /**
   * Update the participants for a new IPO
   *
   * @param {any} state
   * @param {any} participants
   */
  [mutationTypes.UPDATE_NEW_IPO_PARTICIPANTS](
    state,
    participants: Array<IParticipant>
  ) {
    state.new_ipo.participants = participants;
  },

  [mutationTypes.UPDATE_IPO_PARTICIPANT_PARTY_TYPES_STATE](
    state,
    newParticipantTypes
  ) {
    state.new_ipo.participant_party_types = {
      ...state.new_ipo.participant_party_types,
      ...newParticipantTypes
    };
  },

  /**
   * Store the list of capital market operators into the store
   *
   * @param {any} state
   * @param {any} capitalMarketPlayers
   */
  [mutationTypes.STORE_CAPITAL_MARKET_PLAYERS](state, capitalMarketPlayers) {
    state.capital_market_players = [...capitalMarketPlayers];
  },

  /**
   * Store the default participant types in the store
   *
   * @param {any} state
   * @param {any} defaultParticipantTypes
   */
  [mutationTypes.STORE_DEFAULT_PARTICIPANT_TYPES](
    state,
    defaultParticipantTypes
  ) {
    state.default_participant_types = defaultParticipantTypes;
  },

  /**
   * Set the use default party types index
   *
   * @param {any} state
   * @param {any} useDefault
   */
  [mutationTypes.UPDATE_NEW_IPO_USE_DEFAULT_PARTICIPANT_TYPE](
    state,
    useDefault
  ) {
    state.new_ipo.use_default_party_types = useDefault;
  },

  /**
   * Set the use default party types index
   *
   * @param {any} state
   * @param {any} useDefault
   */
  [mutationTypes.UPDATE_MODIFY_IPO_USE_DEFAULT_PARTICIPANT_TYPE](
    state,
    useDefault
  ) {
    state.modify_ipo.use_default_party_types = useDefault;
  },

  [mutationTypes.UPDATE_PARTICIPANT_STATE](state, newParticipant) {
    state.new_ipo.participants = [
      ...state.new_ipo.participants,
      newParticipant
    ];
  },

  /**
   * Change the active index of the IPO creation form stepper.
   *
   * @param {any} state
   * @param {number} stepperIndex
   */
  [mutationTypes.UPDATE_IPO_CREATION_FORM_STEPPER](
    state,
    stepperIndex: number
  ) {
    state.ipoCreationFormStepper = stepperIndex;
  },

  /**
   * Store currently active ipos into the store
   *
   * @param {any} state
   * @param {any} ipos
   */
  [mutationTypes.STORE_IPOS](state, ipos) {
    state.ipos = ipos;
  },

  /**
   * Move the modify ipo form stepper one step backward.
   *
   * @param {any} state
   */
  [mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_BACKWARD](state) {
    state.ipoModificationFormStepper = state.ipoModificationFormStepper - 1;
  },

  /**
   * Move the modify ipo form stepper one step forward.
   *
   * @param {any} state
   */
  [mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_FORWARD](state) {
    state.ipoModificationFormStepper = state.ipoModificationFormStepper + 1;
  },

  /**
   * Set the IPO to be modified
   *
   * @param {any} state
   * @param {any} ipoID
   */
  [mutationTypes.SET_IPO_TO_MODIFY](state, ipoID) {
    let ipos = state.ipos;
    ipos = ipos.filter(ipo => ipo.id === ipoID);

    if (ipos.length) {
      state.modify_ipo = {
        ...state.modify_ipo,
        ...ipos[0]
      };
    }
  },

  /**
   * Change the active index of the IPO modification form stepper.
   *
   * @param {any} state
   * @param {number} stepperIndex
   */
  [mutationTypes.UPDATE_IPO_MODIFICATION_FORM_STEPPER](
    state,
    stepperIndex: number
  ) {
    state.ipoModificationFormStepper = stepperIndex;
  }
};

export default mutations;



