<template>
    <div>
        <v-dialog v-model="showDeleteResourceDialog" lazy absolute>
            <!--Button used to trigger the opening of the delete user dialog -->
            <v-btn id="openDeleteResourceDialog"
                style="display: none" color="primary" dark slot="activator">
            </v-btn>

            <v-card>
                <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                    <h5 class="white--text font-size-20px">{{ resourcePageTitle }}</h5>
                </v-card-title>
                <v-card-text>
                <p>Are you sure you want to delete?</p>

                <!-- Buttons -->
                <v-container fluid class="p0">
                    <!-- Loading text -->
                    <v-layout row>
                    <v-flex xs12>
                        <!-- Loading Icon when adding a user -->
                        <loading v-if="isDeletingResource" :loadingText="'Deleting...'" class="mt20"></loading>
                    </v-flex>
                    </v-layout>
                    <v-layout  row class="mt20">
                    <v-flex xs6 class="d-flex justify-end ">
                        <v-btn small 
                        style="background: #4c7396; color: #FFFFFF"
                            :loading="isDeletingResource"
                            :disabled="isDeletingResource"
                        @click="_deleteResource(resource)">
                        YES DELETE
                        </v-btn>
                    </v-flex>

                    <v-flex xs6>
                        <v-btn small class="red darken-1 white--text"
                        @click="closeDeleteResourceDialog()">
                        CANCEL
                        </v-btn>
                    </v-flex>
                    </v-layout>
                </v-container>
                </v-card-text>
            </v-card>
        </v-dialog>

        <!-- Snackbar to show unsuccessful actions -->
        <v-snackbar error
            :timeout="snackbarTimeout"
            :top="true"
            :color="'error'"
            v-model="resourceActionFailed">
            {{ resourceErrorMessage }}
        </v-snackbar>
        
        <!-- Snackbar showing successful actions -->
        <v-snackbar success
            :timeout="snackbarTimeout"
            :top="true"
            :color="'success'"
            v-model="resourceActionSuccessful">
            {{ resourceSuccessMessage }}
        </v-snackbar>
    </div>

</template>


<script>
import Loading from "./Loading";

export default {
  props: {
    resource: {
      type: Object,
      default: {}
    },
    resourcePageTitle: {
      type: String,
      default: "Delete Resource"
    },
    deleteResource: {
      type: Function,
      default: () => {}
    },
    refreshResource: {
      type: Function,
      default: () => {}
    }
  },

  components: {
    Loading
  },

  data() {
    return {
      showDeleteResourceDialog: false,
      isDeletingResource: false,
      resourceActionFailed: false,
      resourceActionSuccessful: false,
      resourceErrorMessage: "An error occurred",
      resourceSuccessMessage: "Successfully completed",
      snackbarTimeout: 4000
    };
  },

  methods: {
    _deleteResource: function(resource) {
      this.isDeletingResource = true;

      this.deleteResource(resource).then(
        response => {
          this.isDeletingResource = false;
          this.resourceSuccessMessage = "Deleted Successfully";
          this.resourceActionSuccessful = true;
          this.refreshResource();
          this.closeDeleteResourceDialog();
        },
        err => {
          console.log(err);
          this.isDeletingResource = false;
          this.resourceErrorMessage = "Failed to delete. Please try again";
          this.resourceActionFailed = true;
          this.refreshResource();
        }
      );
    },

    closeDeleteResourceDialog: function() {
      this.showDeleteResourceDialog = false;
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



