<template>
  <v-container fluid class="" justify-center align-items-center>

    <!-- FORM INPUTS -->
    <v-layout row>
        <v-flex xs12 sm10 lg6>
          <v-layout row justify-center class="d-flex">
          <v-form v-model="valid" ref="ipoDataForm" class="w100p pl5">

            <!-- Issuer Name -->
            <v-flex xs12 class="height-55px mb6">
                <v-text-field   :label="'ISSUER NAME'" v-model="modify_ipo.issuer_name" 
                :type="'text'" :rules="requiredRule"  v-validate="'required'">
                </v-text-field>
            </v-flex>

            <!-- Type of IPO -->
            <v-flex xs12 class="height-55px mb6">
              <v-select class="" :label="'TYPE OF IPO'" :items="ipoTypes" 
              v-model="modify_ipo.type" v-validate="'required'" :rules="requiredRule">
              </v-select>
            </v-flex>

            <!-- Price -->
            <v-flex  xs12 sm12 class="height-55px mb6" v-if="isFixedPriceIpo()">
              <p class="mt20 mb0 red-text" style="color: red" v-if="inValidFixedPrice">Please provide a valid fixed price</p>
                <v-text-field   :label="'PRICE'" v-model="modify_ipo.price" 
                :type="'number'">
                </v-text-field>
            </v-flex>
            
             
             <!-- Min Price (book building only) -->
             <v-flex  xs12 sm12 class="height-55px mb6" v-if="isBookBuildingIpo()">
                <v-text-field   :label="'PRICE GUIDANCE: LOW'" v-model="modify_ipo.price_guidance.min" 
                :type="'number'" :rules="requiredRule"  v-validate="'required'">
                </v-text-field>
            </v-flex>

             <!-- Max Price (book building only) -->
             <v-flex  xs12 sm12 class="height-55px mb6" v-if="isBookBuildingIpo()">
                <v-text-field   :label="'PRICE GUIDANCE: HIGH'" v-model="modify_ipo.price_guidance.max" 
                :type="'number'" :rules="requiredRule"  v-validate="'required'" name="ipoInfo.max_price" >
                </v-text-field>
            </v-flex>

            <p class="w100p mt30 mb0"><b>UNITS ON OFFER</b></p>
             <!-- Primary Units on offer -->
            <v-flex  xs12 sm12 class="height-55px mb6">
                <v-text-field   :label="'PRIMARY UNITS ON OFFER'" v-model="modify_ipo.primary_units_on_offer" 
                :type="'number'" :rules="requiredRule"  v-validate="'required'">
                </v-text-field>
            </v-flex>
            <!-- Secondary Units on offer -->
            <v-flex  xs12 sm12 class="height-55px mb6">
                <v-text-field   :label="'SECONDARY UNITS ON OFFER'" v-model="modify_ipo.secondary_units_on_offer" 
                :type="'number'">
                </v-text-field>
            </v-flex>
            <!-- Total units on offer -->
            <v-flex  xs12 sm12 class="height-55px mb6">
                <v-text-field   :label="'TOTAL UNITS ON OFFER'" v-model="total_units_on_offer" 
                :type="'number'" :disabled="true">
                </v-text-field>
            </v-flex>
            <!-- Over allotment provision -->
            <v-flex xs12>
               <v-checkbox :label="'OVER ALLOTMENT PROVISION?'" v-model="modify_ipo.allow_over_allotment" light></v-checkbox>
            </v-flex>
            <!-- Shares for overallotment -->
            <v-flex  xs12 sm12 class="height-55px mb6" v-if="modify_ipo.allow_over_allotment">
                <v-text-field   :label="'NO OF SHARES FOR OVER ALLOTMENT'" v-model="modify_ipo.units_for_over_allotment" 
                :type="'number'" :disabled="!modify_ipo.allow_over_allotment">
                </v-text-field>
            </v-flex>

            <!-- START DATE -->
            <v-flex xs12>
              <v-menu
                lazy
                :close-on-content-click="false"
                v-model="startDateMenu"
                transition="scale-transition"
                offset-y
                full-width
                :nudge-left="40"
              >
              <v-text-field
                slot="activator"
                label="OFFER OPEN DATE"
                v-model="formattedOfferStartDate"
                v-validate="'required'" :rules="requiredRule"
                prepend-icon="event"
                readonly
              >
              </v-text-field>
              <v-date-picker
                v-model="modify_ipo.open_date"
                v-validate="'required'" :rules="requiredRule"
                :date-format="date => new Date(date).toDateString()"
                :formatted-value.sync="formattedOfferStartDate"
                scrollable
                autosave>
              </v-date-picker>
              </v-menu>

            </v-flex>
            <!-- EOF START DATE -->

              <!-- END DATE -->
            <v-flex xs12>
              <v-menu
                lazy
                :close-on-content-click="false"
                v-model="endDateMenu"
                transition="scale-transition"
                offset-y
                full-width
                :nudge-left="40"
              >
              <v-text-field
                slot="activator"
                label="OFFER CLOSE DATE"
                v-model="formattedOfferEndDate"
                prepend-icon="event"
                readonly
              >
              </v-text-field>
              <v-date-picker
                v-model="modify_ipo.close_date"
                v-validate="'required'" :rules="requiredRule"
                :date-format="date => new Date(date).toDateString()"
                :formatted-value.sync="formattedOfferEndDate"
                scrollable
                autosave>
              </v-date-picker>
              </v-menu>

            </v-flex>
            <!-- END DATE -->

          </v-form>
          </v-layout>
        </v-flex>
    </v-layout>

    <!-- Buttons -->
    <v-layout row>
      <v-btn error @click="goBack()">Back</v-btn>
      <v-btn primary @click="moveToParticipantSetup()">Next</v-btn>
    </v-layout>

  </v-container>
</template>

<script>
import _ from "lodash";
import * as mutationTypes from "../../../store/mutation-types";
import { IpoType } from "../../../models/ipo.interface";

export default {
  data() {
    return {
      IpoType,
      requiredRule: [v => !!v || "Required"],
      modify_ipo: _.cloneDeep(this.$store.state.eipo_registrar.modify_ipo),
      formattedOfferStartDate: null,
      formattedOfferEndDate: null,
      startDateMenu: false,
      endDateMenu: false,
      valid: false,
      inValidFixedPrice: false
    };
  },

  watch: {
    modify_ipo: {
      handler: _.debounce(function(newIpoData) {
        this.$store.commit(mutationTypes.UPDATE_MODIFY_IPO_STATE, newIpoData);
      }, 0),
      deep: true
    },

    total_units_on_offer: {
      handler: _.debounce(function(total) {
        this.modify_ipo.total_units_on_offer = total;
      }, 0)
    }
  },

  computed: {
    // Calculate the total number of units offered for an IPO.
    total_units_on_offer() {
      if (
        this.modify_ipo.primary_units_on_offer ||
        this.modify_ipo.secondary_units_on_offer
      ) {
        return (
          parseFloat(this.modify_ipo.primary_units_on_offer) +
          parseFloat(this.modify_ipo.secondary_units_on_offer)
        );
      }
    },

    ipoTypes() {
      return [
        {
          text: "BOOK BUILDING",
          value: IpoType.BOOK_BUILDING
        },
        {
          text: "FIXED PRICE",
          value: IpoType.FIXED_PRICE
        }
      ];
    }
  },

  methods: {
    isFixedPriceIpo() {
      return this.modify_ipo.type === this.IpoType.FIXED_PRICE;
    },
    isBookBuildingIpo() {
      return this.modify_ipo.type === this.IpoType.BOOK_BUILDING;
    },
    fixedPriceIsValid() {
      if (this.isFixedPriceIpo()) {
        if (!this.modify_ipo.price) {
          return false;
        }
      }

      return true;
    },
    goBack() {
      this.$store.commit(mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_BACKWARD);
    },
    moveToParticipantSetup() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.ipoDataForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          // Ensure that fixed price IPO's have a fixed price
          if (!this.fixedPriceIsValid()) {
            this.inValidFixedPrice = true;
            console.log("Error");
            return;
          }

          // Navigate to the next step
          this.$store.commit(
            mutationTypes.UPDATE_IPO_MODIFICATION_FORM_STEPPER,
            3
          );
        }
      });
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



