<template>
  <v-container fluid>
      <!-- MultiStep form used in the IPO modification process -->
      <v-stepper v-model="ipoModificationFormStepper">

        <!-- IPO MODIFICATION FORM headers -->
        <v-stepper-header>
           <v-stepper-step step="1" :complete="ipoModificationFormStepper > 1">Select IPO</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="2" :complete="ipoModificationFormStepper > 2">IPO Data</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="3" :complete="ipoModificationFormStepper > 3">Offer Participant Setup</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="4" :complete="ipoModificationFormStepper > 4">Select Parties</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="5" :complete="ipoModificationFormStepper > 5">Review Data</v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step step="6" >Approval</v-stepper-step>
        </v-stepper-header> 
        <!-- EOF IPO MODIFICATION FORM headers -->

        <!-- Select the IPO to be modified -->
        <v-stepper-content step="1">
          <select-ipo></select-ipo>
        </v-stepper-content>
        <!-- EOF Select the IPO to be modified -->

        <!-- Input the IPO creation data -->
        <v-stepper-content step="2">
          <ipo-data></ipo-data>
        </v-stepper-content>
        <!-- EOF IPO creation data -->

        <!-- Offer participant setup -->
        <v-stepper-content step="3">
          <offer-participant-setup></offer-participant-setup>
        </v-stepper-content>
        <!-- EOF Offer participant setup -->

        <!-- Select parties -->
         <v-stepper-content step="4">
          <select-parties></select-parties>
        </v-stepper-content>
        <!-- EOF Select parties -->

        <!-- Review data -->
        <v-stepper-content step="5">
          <review-data></review-data>
        </v-stepper-content>
        <!-- EOF Review data -->

        <!-- IPO Approval -->
        <v-stepper-content step="6">
          <ipo-approval></ipo-approval>
        </v-stepper-content>
        <!-- EOF IPO approval -->

      </v-stepper>
      <!-- EOF MultiStep form used in creating IPO's -->

  </v-container>
</template>


<script>
import { mapGetters } from "vuex";
import * as mutationTypes from "../../store/mutation-types";

import SelectIpo from "./ModifyIPO-forms/SelectIpo";
import IpoData from "./ModifyIPO-forms/IpoData";
import OfferParticipantSetup from "./ModifyIPO-forms/OfferParticipantSetup";
import SelectParties from "./ModifyIPO-forms/SelectParties";
import ReviewData from "./ModifyIPO-forms/ReviewData";
import IpoApproval from "./ModifyIPO-forms/IpoApproval";

export default {
  components: {
    SelectIpo,
    IpoData,
    OfferParticipantSetup,
    SelectParties,
    ReviewData,
    IpoApproval
  },

  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      _ipoModificationFormStepper: "ipoModificationFormStepper"
    }),

    ipoModificationFormStepper: {
      get() {
        return this._ipoModificationFormStepper;
      },
      set(stepperIndex) {
        this.$store.commit(
          mutationTypes.UPDATE_IPO_MODIFICATION_FORM_STEPPER,
          parseInt(stepperIndex)
        );
      }
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



