<template>
    <v-container fluid class="" justify-center align-items-center>

    <!-- FORM INPUTS -->
    <v-layout row>
        <v-flex>
            Please select the IPO to be modified.
        </v-flex>
        <v-flex xs12 sm10 lg6>
          <v-layout row justify-center class="d-flex">
            <v-form v-model="valid" ref="selectIpoForm" class="w100p pl5">

                <!-- SELECT IPO -->
                <v-flex xs12 class="height-55px mb6">
                <v-select class="" :label="'SELECT IPO'" :items="ipos" 
                v-model="selectedIpoID" v-validate="'required'" :rules="requiredRule">
                </v-select>
                </v-flex>

            </v-form>
          </v-layout>
        </v-flex>
    </v-layout>

    <!-- Buttons -->
    <v-layout row class="mt50">
      <!-- <v-btn error >Cancel</v-btn> -->
      <v-btn
      :disabled="settingSelectedIpo" :loading="settingSelectedIpo" 
      primary @click="moveToIpoData()">Next</v-btn>
    </v-layout>

  </v-container>
</template>


<script>
import { mapGetters } from "vuex";

import * as mutationTypes from "../../../store/mutation-types";

export default {
  data() {
    return {
      valid: false,
      selectedIpoID: "",
      requiredRule: [v => !!v || "Required"],
      settingSelectedIpo: false
    };
  },

  computed: {
    ...mapGetters({
      ipos: "iposFormattedForSelect"
    })
  },

  methods: {
    moveToIpoData() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.selectIpoForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          this.settingSelectedIpo = true;

          this.$store.commit(
            mutationTypes.SET_IPO_TO_MODIFY,
            this.selectedIpoID
          );

          setTimeout(() => {
            this.$store.commit(
              mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_FORWARD
            );
            this.settingSelectedIpo = false;
          }, 3000);
        }
      });
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



