import Vue from "vue";
import Router from "vue-router";
import Login from "@/components/Login";
import Dashboard from "@/components/Dashboard";
import PageNotFound from "@/components/404";

/**
 * COMPONENTS FOR EIPO REGISTRAR MODULE
 *
 */
import CreateIPO from "../components/ipo/CreateIPO";
import ModifyIPO from "../components/ipo/ModifyIPO";
import CapitalMarketPlayers from "../components/ipo/CapitalMarketPlayers";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Login",
      component: Login
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: Dashboard
    },
    {
      path: "/registrar/create/ipo",
      name: "createIpo",
      component: CreateIPO
    },
    {
      path: "/registrar/modify/ipo",
      name: "modifyIpo",
      component: ModifyIPO
    },
    {
      path: "/capitalMarketPlayers",
      name: "capitalMarketPlayers",
      component: CapitalMarketPlayers
    },
    {
      path: "*",
      name: "PageNotFound",
      component: PageNotFound
    }
  ]
});



