/**
 * Vuex Store module for the registrar part of the E-IPO process
 */

import getters from "./registrar.getters";
import mutations from "./registrar.mutations";
import { IpoStatus } from "../../../../models/ipo.interface";
import * as mutationTypes from "../../../mutation-types";

const allMutations = {
  ...mutations,

  /**
   * Reset the issuing house module store to its initial state when logging out a user
   * @param state
   */
  [mutationTypes.CLEAR_REGISTRAR_MODULE_STORE](currentState) {
    const initialState = state();
    Object.keys(initialState).forEach(key => {
      currentState[key] = initialState[key];
    });
  }
};

// Initial Store State
const state = () => ({
  capital_market_players: [],
  default_participant_types: [],
  ipoCreationFormStepper: 0,
  ipoModificationFormStepper: 0,
  new_ipo: {
    id: "",
    issuer_name: "",
    type: "",
    price: null,
    price_guidance: {
      min: "",
      max: ""
    },
    primary_units_on_offer: 0,
    secondary_units_on_offer: 0,
    total_units_on_offer: "",
    allow_over_allotment: false,
    units_for_over_allotment: "",
    open_date: "",
    close_date: "",
    use_default_party_types: true,
    participants: [],
    participant_types: [],
    status: IpoStatus.PENDING
  },
  modify_ipo: {
    id: "",
    issuer_name: "",
    type: "",
    price: null,
    price_guidance: {
      min: "",
      max: ""
    },
    primary_units_on_offer: 0,
    secondary_units_on_offer: 0,
    total_units_on_offer: "",
    allow_over_allotment: false,
    units_for_over_allotment: "",
    open_date: "",
    close_date: "",
    use_default_party_types: true,
    participants: [],
    participant_types: [],
    status: IpoStatus.PENDING
  },
  ipos: {}
});

export default {
  state: state(),
  getters,
  mutations: allMutations
};



