import axios from "axios";
import * as ApiUrls from "./ApiUrlService";

/**
 * Create a new Capital Market Player
 * @param data
 */
export const CreateCapitalMarketPlayer = data => {
  return axios({
    method: "post",
    url: ApiUrls.ParticipantsUrl,
    data
  });
};

/**
 * Get a list of all Capital Market Players owned by a broker
 * @param data
 */
export const GetapitalMarketPlayers = data => {
  return axios({
    method: "get",
    url: ApiUrls.ParticipantsUrl,
    data
  });
};

/**
 * Delete Capital Market Player
 * @param data
 */
export const DeleteCapitalMarketPlayer = data => {
  return axios({
    method: "delete",
    url: ApiUrls.ParticipantsUrl + "/" + data.id,
    data
  });
};

/**
 * Delete Capital Market Player
 * @param data
 */
export const EditCapitalMarketPlayer = data => {
  return axios({
    method: "put",
    url: ApiUrls.ParticipantsUrl,
    data
  });
};



