<template>
    <!-- Dialog for viewing a resource -->
    <v-dialog v-model="showViewResourceDialog" lazy absolute>

      <!--Button used to trigger the opening of the edit user dialog -->
      <v-btn id="openViewResourceDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>
        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-20px">{{ resourcePageTitle }}</h5>
        </v-card-title>

        <v-card-text>
          <v-form v-model="valid" ref="form" class="w100p pl5">

            <!-- First Name -->
            <v-flex xs12 class="height-55px mb6">
             <v-text-field
              :label="'NAME'"
              v-model="resource.name"
              :type="'text'"
              name="firstName" disabled>
             </v-text-field>
            </v-flex>

            <!-- Buttons -->
            <v-container fluid class="p0 ">
              <v-layout  row class="mt20">
                <v-flex xs6>
                  <v-btn small class="red darken-1 white--text"
                    @click="closeViewResourceDialog()">
                    CLOSE
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>

          </v-form>
        </v-card-text>

      </v-card>

    </v-dialog>
</template>


<script>
export default {
  props: {
    resource: {
      type: Object,
      default: {}
    },
    show: {
      type: Boolean,
      default: false
    },
    resourcePageTitle: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      valid: false,
      snackbarTimeout: 4000,
      showViewResourceDialog: false,
      isSubmittingBatch: false,
      isDeletingBatch: false,
      batchSubmitted: false,
      batchDeleted: false,
      batchSubmissionFailed: false,
      batchDeletingFailed: false
    };
  },

  computed: {},

  methods: {
    closeViewResourceDialog: function() {
      this.showViewResourceDialog = false;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



