<template>
   <v-container  fluid class="pt0 d-flexx" justify-center>
       <!-- {{ ipoInfo }} -->
      <v-layout row class="w100p wrapper--summary--form">
          
          <h3 class="bg-csp-blue text-white mb0 w100p pt5 pb5 pl10"> IPO SUMMARY </h3>
          <table class="table">
              <tr>
                  <td class="w50p"><b>ISSUER NAME</b></td>
                  <td class="w50p"> {{new_ipo.issuer_name | uppercase}} </td>
              </tr>
               <tr>
                  <td><b>TYPE OF IPO</b></td>
                  <td>{{ipoType | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>PRIMARY UNITS ON OFFER</b></td>
                  <td>{{new_ipo.primary_units_on_offer | currency('',0)}}</td>
              </tr>
               <tr>
                  <td><b>SECONDARY UNITS ON OFFER</b></td>
                  <td>{{new_ipo.secondary_units_on_offer | currency('',0)}}</td>
              </tr>
                <tr>
                  <td><b>OFFER OPEN DATE</b></td>
                  <td>{{new_ipo.open_date | moment('DD-MMM-YYYY') | uppercase }}</td>
                </tr>
                <tr>
                  <td><b>OFFER CLOSE DATE</b></td>
                  <td>{{new_ipo.close_date | moment('DD-MMM-YYYY') | uppercase }}</td>
               </tr>

              <tr class="bg-csp-light-blue text-white w100p" 
              >
                  <td class=""><b>IPO PARTICIPANTS</b></td>
                   <td class="bg-csp-light-blue"></td>
              </tr>

               <tr >
                  <td> <b>PARTY NAME</b> </td>
                  <td> <b>PARTY FUNCTION</b> </td>
               </tr>
                <tr v-for="(participant, i) in participants" :key="i">
                  <td>  {{ participant.name | uppercase }}</td>
                  <td>  {{ participant.type | uppercase }} </td>
               </tr>
          </table>
      </v-layout>

     <!-- Loading Icon when submitting ipo requests-->
      <v-layout v-if="isSubmittingIpoRequest"  row class=" d-flex justify-center mt20 mb20">
        <v-flex class="xs6 d-flex justify-center align-center">
          <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
          <span class="font-size-12 ml5">Creating New IPO ...</span>
        </v-flex>
      </v-layout>

    <!-- Buttons -->
    <v-layout row class="w100p">
      <v-btn error @click="goBack()">Go Back</v-btn>
      <v-btn primary @click="submitApplication()" :disabled="isSubmittingIpoRequest" :loading="isSubmittingIpoRequest">
        Create IPO</v-btn>
    </v-layout>

    <!-- Snackbar to show success message when ipo is created -->
    <v-snackbar success
    :timeout="snackbarTimeout"
    :top="true"
    :color="'success'"
    v-model="showIpoSubmittedSnackbar"
    >
    IPO created successfully
    </v-snackbar>

    <!-- Snackbar to show error message when creating IPO's fail -->
    <v-snackbar error
    :timeout="snackbarTimeout"
    :top="true"
    :color="'error'"
    v-model="showIpoSubmittedErrorSnackbar"
    >
    An error occured while creating the IPO
    </v-snackbar>

  </v-container>
</template>

<script>
// import { ipoTypes } from "../data-constants/data_old";
import { IpoType } from "../../../models/ipo.interface";
// import _ from "lodash";

import { CreateIpo } from "../../../services/IpoService";
import * as mutationTypes from "../../../store/mutation-types";

export default {
  data() {
    return {
      // ipoTypes,
      snackbarTimeout: 4000,
      inPreviewState: false,
      isSubmittingIpoRequest: false,
      showIpoSubmittedSnackbar: false,
      showIpoSubmittedErrorSnackbar: false
    };
  },

  computed: {
    new_ipo() {
      return this.$store.state.eipo_registrar.new_ipo;
    },
    ipoType() {
      const type = this.new_ipo.type;
      let ipoType = "";

      if (type === IpoType.FIXED_PRICE) {
        ipoType = "FIXED PRICE";
      }

      if (type === IpoType.BOOK_BUILDING) {
        ipoType = "BOOK BUILDING";
      }

      return ipoType;
    },
    isFixedPriceIpo() {
      return this.new_ipo.type === IpoType.FIXED_PRICE;
    },
    isBookBuildingIpo() {
      return this.new_ipo.type === IpoType.BOOK_BUILDING;
    },
    participantTypes() {
      return this.$store.state.eipo_registrar.new_ipo.participant_party_types;
    },
    participants() {
      // return this.$store.state.eipo_registrar.new_ipo.participants;

      const usingDefaultPartyTypes = this.$store.state.eipo_registrar.new_ipo
        .use_default_party_types;
      // const capitalMarketPlayers = this.$store.state.eipo_registrar
      //   .capital_market_players;
      // const participantTypes = this.$store.state.eipo_registrar
      //   .default_participant_types;
      const participants = this.$store.state.eipo_registrar.new_ipo
        .participants;

      let ipoParticipants = [];
      if (usingDefaultPartyTypes) {
        participants.forEach(participant => {
          ipoParticipants.push({
            name: participant.name,
            type: participant.type
          });
        });
      } else {
        participants.forEach(participant => {
          ipoParticipants.push({
            name: participant.name,
            type: participant.type
          });
        });
      }

      // participants.forEach(participant => {
      //   // Find each participant in the list of capital market players
      //   let capitalMarketPlayer = capitalMarketPlayers.filter(
      //     player => player.value === participant.name
      //   );

      //   // Find each participant Type in the list of participant types
      //   let participantType = participantTypes.filter(
      //     participantType => participantType.value === participant.type
      //   );

      //   ipoParticipants.push({
      //     // name: capitalMarketPlayer[0].text,
      //     name: capitalMarketPlayer[0].text,
      //     type: participantType[0].text
      //   });
      // });

      return ipoParticipants;
    }
  },

  methods: {
    /**
     * Submit the request to create a new IPO
     */
    submitApplication() {
      const requestData = {
        ...this.$store.state.eipo_registrar.new_ipo,
        registrar_id: 1,
        issuing_house_id: 2,
        user_id: 6
      };
      // delete requestData.user_id;
      // requestData.ipo_info.user_id = this.$store.state.eipo_registrar.user_id;

      console.log(requestData);
      this.isSubmittingIpoRequest = true;

      CreateIpo(requestData)
        .then(response => {
          console.log(response);
          this.showIpoSubmittedSnackbar = true;
          this.isSubmittingIpoRequest = false;
          this.goToApprovalSegment();
        })
        .catch(error => {
          console.log(error);
          this.showIpoSubmittedErrorSnackbar = true;
          this.isSubmittingIpoRequest = false;
        });
    },

    goBack() {
      this.$store.commit(mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_BACKWARD);
    },

    goToApprovalSegment() {
      this.$store.commit(mutationTypes.MOVE_CREATE_IPO_FORM_STEPPER_FORWARD);
    }
  }
};
</script>

<style lang="sass" scoped>
    .wrapper--summary--form
        border: 2px solid #1a2155
</style>



