<template>
   <v-container fluid class="height-400pxx" justify-center>
    <v-layout row>
        <v-flex></v-flex>

        <!-- FORM INPUTS -->
        <v-flex xs12 sm10 lg6>
          <v-layout row justify-center class="d-flex">

            <v-flex xs12>
               <v-checkbox :label="'USE DEFAULT TYPES'" v-model="use_default_party_types" light></v-checkbox>
            </v-flex>

            <template>
              <v-flex xs12 class="height-55px mb6" v-for="(participant, index) in 5" :key="index">
                  <v-text-field 
                  :label="`TYPE OF PARTY ${index+1}`" v-model="ipoParticipantTypes[index]" 
                  :type="'text'"
                  :disabled="use_default_party_types">
                  </v-text-field>
              </v-flex>
            </template>
  
          </v-layout>
        </v-flex>

        <v-flex></v-flex>
    </v-layout>

       <!-- Buttons -->
    <v-layout row>
      <v-btn error @click="goBack()">Previous</v-btn>
      <v-btn primary @click="moveToSelectParties()">Next</v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import _ from "lodash";
import * as mutationTypes from "../../../store/mutation-types";

export default {
  data() {
    return {
      use_default_party_types: _.cloneDeep(
        this.$store.state.eipo_registrar.modify_ipo.use_default_party_types
      ),
      requiredRule: [v => !!v || "Required"],
      ipoParticipantTypes: []
    };
  },

  watch: {
    ipoParticipantTypes: {
      handler: _.debounce(function(participantTypes) {
        this.$store.commit(
          mutationTypes.UPDATE_MODIFY_IPO_PARTICIPANT_TYPES,
          participantTypes
        );
      }, 0),
      deep: true
    },

    use_default_party_types: {
      handler: _.debounce(function(useDefault) {
        this.$store.commit(
          mutationTypes.UPDATE_MODIFY_IPO_USE_DEFAULT_PARTICIPANT_TYPE,
          useDefault
        );
      })
    }
  },

  methods: {
    goBack() {
      this.$store.commit(mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_BACKWARD);
    },

    moveToSelectParties() {
      this.$store.commit(mutationTypes.MOVE_MODIFY_IPO_FORM_STEPPER_FORWARD);
    }
  }
};
</script>

<style lang="sass" scoped>

</style>



