import axios from "axios";
import * as ApiUrlService from "./ApiUrlService";

/**
 * Authenticate an issuing house user
 *
 * @param credentials
 */
export const AuthenticateUser = credentials => {
  return axios({
    url: ApiUrlService.AuthenticateRegistrar,
    method: "post",
    data: credentials
  });
};



