<template>
    <v-container fluid class="pt0">

        <!-- Header --> 
        <v-card class="elevation-0 mb0 row d-flex flex-column ml1 mr1"
            style="background: transparent; border: none">
            <v-container fluid class="pl1 pr1 pt2">
                <v-progress-linear v-if="fetchingResources" v-bind:indeterminate="fetchingResources"></v-progress-linear>
                <v-layout row>
                <v-flex xs5 class="d-flex align-items-end">
                    <h3 class="csp-light-blue-text font-size-20">{{ PageTitle }}</h3>
                </v-flex>

                <v-flex xs7 d-flex class="">
                    <v-container fluid class="p0">
                    <v-layout row class="">
                        <v-flex xs6>
                        </v-flex>
                        <v-flex d-flex xs6 class="">
                            <v-btn @click="showResourceDialog('ADD', null, $event)" info  class="add-new-btn font-size-12 p0 ml-auto mb0 mr0">
                                Add New
                            </v-btn>
                        </v-flex>
                    </v-layout>
                    </v-container>
                </v-flex>
                </v-layout>
            </v-container>
        </v-card>
        
        <!-- Table -->
        <v-card class="elevation-0 mb10">
            <div style="overflow-y: scroll"> <!-- Used so the data table scrolls -->
                <v-data-table
                    hide-actions
                    :items="Resource"
                    :search="search"
                    :loading="loadingDataTable"
                    :rows-per-page-items="rowsPerPageItems"
                    class="elevation-1 DataTable table-striped table-bordered">
                    <template slot="headers" scope="props">
                    <tr class="bg-csp-light-blue DataTableHeader">
                        <th class="text-left">ID</th>
                        <th class="text-left">NAME</th>
                        <th class="text-center width-80px white--text ">ACTIONS</th>
                    </tr>
                    </template>

                    <template slot="items" scope="props">
                        <tr>
                            <td>{{ props.item.id}}</td>
                            <td>{{ props.item.name }}</td>
                            <td class="font-size-10 text-center width-80px d-flex align-center justify-center" style="padding: 0px; border: 0px">
                                <v-btn
                                    data-toggle="tooltip" data-placement="top" title="EDIT" icon class="m0 p0 width-30px height-30px"
                                    @click="showResourceDialog('EDIT',props.item, $event)">
                                    <v-icon  class="blue--text font-weight-bold font-size-18" >
                                    mode_edit
                                    </v-icon>
                                </v-btn>
                                <v-btn
                                    data-toggle="tooltip" data-placement="top" title="VIEW"
                                    icon class="m0 p0 width-30px height-30px"
                                    @click="showResourceDialog('VIEW',props.item, $event)">
                                    <v-icon class="color-csp-blue ml4 fa fa-eye fa-2x btn-icon font-weight-bold font-size-18">
                                    </v-icon>
                                </v-btn>
                                <v-btn
                                    data-toggle="tooltip" data-placement="top" title="DELETE"
                                    icon class="m0 p0 width-30px height-30px"
                                    @click="showResourceDialog('DELETE',props.item, $event)">
                                    <v-icon  class="red--text font-weight-bold font-size-18" >
                                        close
                                    </v-icon>
                                </v-btn>
                            </td>
                        </tr>
                    </template>
                </v-data-table>
            </div>
        </v-card>

        <!-- Snackbar to show unsuccessful actions -->
        <v-snackbar error
            :timeout="snackbarTimeout"
            :top="true"
            :color="'error'"
            v-model="resourceActionFailed">
            {{ resourceErrorMessage }}
        </v-snackbar>
        
        <!-- Snackbar showing successful actions -->
        <v-snackbar success
            :timeout="snackbarTimeout"
            :top="true"
            :color="'success'"
            v-model="resourceActionSuccessful">
            {{ resourceSuccessMessage }}
        </v-snackbar>

        <view-resource
            :resource = "resource"
            :resourcePageTitle = "resourcePageTitle">
        </view-resource>

        <delete-resource
            :resource = "resource"
            :resourcePageTitle = "resourcePageTitle"
            :deleteResource = "DeleteResource"
            :refreshResource = "RefreshResource">
        </delete-resource>

        <add-resource
            :resourcePageTitle = "resourcePageTitle"
            :addResource = "CreateResource"
            :refreshResource = "RefreshResource">
        </add-resource>

  </v-container>
</template>

<script>
import ViewResource from "./ViewResource";
import DeleteResource from "./DeleteResource";
import AddResource from "./AddResource";

export default {
  components: {
    ViewResource,
    DeleteResource,
    AddResource
  },

  props: {
    Resource: {
      type: Array,
      default: () => []
    },
    CreateResource: {
      type: Function,
      default: () => {}
    },
    DeleteResource: {
      type: Function,
      default: () => {}
    },
    EditResource: {
      type: Function,
      default: () => {}
    },
    RefreshResource: {
      type: Function,
      default: () => {}
    },
    PageTitle: {
      type: String,
      default: ""
    }
  },

  data() {
    return {
      rowsPerPageItems: [10, 15, 20, 25, { text: "All", value: -1 }],
      search: "",
      loadingDataTable: false,
      resource: {},
      resourcePageTitle: "",
      resourceActionFailed: false,
      resourceActionSuccessful: false,
      fetchingResources: false,
      resourceErrorMessage: "An error occurred",
      resourceSuccessMessage: "Successfully completed",
      snackbarTimeout: 4000
    };
  },

  methods: {
    showResourceDialog: function(action, resource, event) {
      this.resource = resource;

      if (action === "VIEW") {
        this.resourcePageTitle = `${resource.name} details`;
        document.querySelector("#openViewResourceDialog").click();
      }

      if (action === "DELETE") {
        this.resourcePageTitle = `Delete ${resource.name}`;
        document.querySelector("#openDeleteResourceDialog").click();
      }

      if (action === "ADD") {
        this.resourcePageTitle = "Create New";
        document.querySelector("#openAddResourceDialog").click();
      }

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      event.stopPropagation();
    }
  }
};
</script>


<style lang="sass" scoped>
  .add-new-btn
    background: #324e85 !important
    border-radius: 5px
    border: none
    padding: 5px 10px !important
  .DataTable
    margin-bottom: 0px !important

  .DataTableHeader,
  .DataTableBody
    height: 25px

  .DataTableHeader
    th
      padding: 5px 5px !important
      color: #FFFFFF !important
      font-weight: bold
      font-size: 10px

  .DataTableBody
    border-bottom: none !important
    &:hover
      background: #CCC !important
      cursor: pointer

  .DataTableBody
    td
      padding: 5px 5px !important
      height: 25px
      color: #31708f
    td:last-child
      border-bottom: 0px solid transparent !important
  .btn__content
    padding: 0px !important
  .eemulate-link
    &:hover
      transition: all .2s
      text-decorations: underline
      border-bottom: 2px solid #1a2155
      padding-bottom: 2px
      cursor: pointer
      background: #EEEEEE

  .selectedRow
    background: #b0bed9 !important
    font-weight: bold
</style>



