<template>
    <div>
        <v-dialog v-model="showAddResourceDialog" lazy absolute>
            <!--Button used to trigger the opening of the edit user dialog -->
            <v-btn id="openAddResourceDialog"
                style="display: none" color="primary" dark slot="activator">
            </v-btn>

            <v-card>
                <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
                <h5 class="white--text font-size-20px">{{ resourcePageTitle }}</h5>
                </v-card-title>

                <v-card-text>
                <v-form v-model="valid" ref="addResourceForm" class="w100p pl5">

                    <!-- First Name -->
                    <v-flex xs12 class="height-55px mb6">
                    <v-text-field
                    :label="'NAME'"
                    v-model="name"
                    :type="'text'"
                    v-validate="'required'"
                    :rules="requiredRule">
                    
                    </v-text-field>
                    </v-flex>

                    <!-- Buttons -->
                    <v-container fluid class="p0 ">
                        <!-- Loading text -->
                    <v-layout row>
                    <v-flex xs12>
                        <!-- Loading Icon when adding a user -->
                        <loading v-if="isAddingResource" :loadingText="'Creating...'" class="mt20"></loading>
                    </v-flex>
                    </v-layout>
                    <v-layout  row class="mt20">
                        <v-flex xs6>
                        <v-btn 
                            :loading="isAddingResource"
                            :disabled="isAddingResource"
                            small 
                            class="info darken-1 white--text"
                            @click="_addResource()">
                            CREATE
                        </v-btn>
                        </v-flex>
                        <v-flex xs6>
                        <v-btn small class="red darken-1 white--text"
                            @click="closeAddResourceDialog()">
                            CLOSE
                        </v-btn>
                        </v-flex>
                    </v-layout>
                    </v-container>

                </v-form>
                </v-card-text>

            </v-card>
        </v-dialog>

        <!-- Snackbar to show unsuccessful actions -->
        <v-snackbar error
            :timeout="snackbarTimeout"
            :top="true"
            :color="'error'"
            v-model="resourceActionFailed">
            {{ resourceErrorMessage }}
        </v-snackbar>
        
        <!-- Snackbar showing successful actions -->
        <v-snackbar success
            :timeout="snackbarTimeout"
            :top="true"
            :color="'success'"
            v-model="resourceActionSuccessful">
            {{ resourceSuccessMessage }}
        </v-snackbar>
    </div>
</template>


<script>
import Loading from "./Loading";
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    resourcePageTitle: {
      type: String,
      default: ""
    },
    addResource: {
      type: Function,
      default: () => {}
    },
    refreshResource: {
      type: Function,
      default: () => {}
    }
  },

  components: {
    Loading
  },

  data() {
    return {
      valid: false,
      snackbarTimeout: 4000,
      showAddResourceDialog: false,
      isAddingResource: false,
      resourceActionFailed: false,
      resourceActionSuccessful: false,
      resourceErrorMessage: "An error occurred",
      resourceSuccessMessage: "Successfully created",
      requiredRule: [v => !!v || "Required"],
      name: ""
    };
  },

  computed: {},

  methods: {
    closeAddResourceDialog: function() {
      this.showAddResourceDialog = false;
    },

    _addResource: function() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          // Display the validation error for each form component
          let formComponents = this.$refs.addResourceForm.$children;
          formComponents.forEach(formComponent => {
            formComponent.shouldValidate = true;
          });
        } else {
          const newResource = {
            name: this.name,
            description: "A player in the capital market"
          };

          this.isAddingResource = true;

          this.addResource(newResource).then(
            response => {
              this.isAddingResource = false;
              this.resourceSuccessMessage = "Successfully created";
              this.resourceActionSuccessful = true;
              this.name = "";
              this.refreshResource();
              this.closeAddResourceDialog();
            },
            err => {
              console.log(err);
              this.isAddingResource = false;
              this.resourceErrorMessage = "An error occured, please try again";
              this.resourceActionFailed = true;
            }
          );
        }
      });
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



