/**
 * ENUM for ipo types
 *
 * @export
 * @enum {number}
 */
export enum IpoType {
  FIXED_PRICE = 1,
  BOOK_BUILDING = 2
}

/**
 * ENUM for ipo status
 *
 * @export
 * @enum {number}
 */
export enum IpoStatus {
  PENDING = 1,
  OPEN = 2,
  ALLOCATING = 3,
  ALLOCATED = 4,
  CLOSED = 5
}

/**
 * Interface defining a participant in an IPO
 *
 * @export
 * @interface IParticipant
 */
export interface IParticipant {
  type: string;
  name: string;
  logo: string;
}

export interface IPriceGuidance {
  min: number;
  max: number;
}

/**
 * Interface defining an IPO
 *
 * @export
 * @interface IpoInterface
 */
export interface IpoInterface {
  id: number;
  issuing_house_id: number;
  registrar_id: number;
  issuer_name: string;
  type: IpoType;
  price?: number;
  price_guidance: IPriceGuidance;
  primary_units_on_offer: number;
  secondary_units_on_offer: number;
  total_units_on_offer: number;
  allow_over_allotment: boolean;
  units_for_over_allotment?: number;
  open_date: string;
  close_date: string;
  use_default_party_types: boolean;
  participants: Array<IParticipant>;
  participant_types: Array<any>;
  status: IpoStatus;
}

export interface ICapitalMarketPlayer {
  id: number;
  name: string;
}

/**
 * Shape of the response expected after authenticating registrars
 *
 * @export
 * @interface IRegistrarAuthenticationResponse
 */
export interface IRegistrarAuthenticationResponse {
  ipos: Array<IpoInterface>;
  capital_market_players: Array<ICapitalMarketPlayer>;
  default_ipo_participant_types: Array<string>;
}



