/**
 * This service handles all IPO API calls.
 *
 */

import axios from "axios";

// // Service exposing the urls for the API
import * as ApiUrls from "./ApiUrlService";

/**
 * Returns a list of all capital market players
 *
 */
export const GetCapitalMarketPlayers = () => {
  return axios({
    url: ApiUrls.ParticipantsUrl,
    method: "get"
  });
};

/**
 * Get the default participant party types
 */
export const GetParticipantPartyTypes = () => {
  return axios({
    url: ApiUrls.PartyTypesUrl,
    method: "get"
  });
};

/**
 * Create a new Initial public offering
 *
 * @param newIpo
 */
export const CreateIpo = newIpo => {
  return axios({
    url: ApiUrls.IpoResourceUrl,
    method: "post",
    data: newIpo
  });
};

/**
 * Modify a new Initial public offering
 *
 * @param newIpo
 */
export const ModifyIpo = newIpo => {
  return axios({
    url: ApiUrls.IpoResourceUrl + "/" + newIpo.id,
    method: "put",
    data: newIpo
  });
};



