import { formatParticipationTypes } from "../../../../services/UtilityService";
import { IpoInterface } from "../../../../models/ipo.interface";

const getters = {
  // Stepper index for the ipo creation form
  ipoCreationFormStepper: state => {
    return state.ipoCreationFormStepper;
  },

  // Stepper index for the ipo modification form
  ipoModificationFormStepper: state => {
    return state.ipoModificationFormStepper;
  },

  /**
   * Get the participant types to be used on the select parties page
   */
  participantTypes: state => {
    const useDefaultParticipantTypes = state.new_ipo.use_default_party_types;
    let participantTypes = [];

    if (useDefaultParticipantTypes) {
      participantTypes = state.default_participant_types;
    } else {
      participantTypes = formatParticipationTypes(
        state.new_ipo.participant_types
      );
    }

    return participantTypes;
  },

  /**
   * Get the participant types to be used on the select parties page for modification
   */
  modifyIpoParticipantTypes: state => {
    const useDefaultParticipantTypes = state.modify_ipo.use_default_party_types;
    let participantTypes = [];

    if (useDefaultParticipantTypes) {
      participantTypes = state.default_participant_types;
    } else {
      participantTypes = formatParticipationTypes(
        state.modify_ipo.participant_types
      );
    }

    return participantTypes;
  },

  /**
   * Get a list of all capital market players
   */
  capitalMarketPlayers: state => {
    return state.capital_market_players;
  },

  formattedCapitalMarketPlayers: state => {
    let players = state.capital_market_players;
    players = players.map(player => {
      return {
        text: player.name,
        value: player.id
      };
    });

    return players;
  },

  /**
   * Get a list of all IPOS selected in the proper format for the v-select component
   */
  iposFormattedForSelect: state => {
    const ipos: Array<IpoInterface> = state.ipos ? state.ipos : [];
    let formattedIpos = [];

    if (ipos) {
      ipos.map(ipo => {
        formattedIpos.push({
          text: ipo.issuer_name,
          value: ipo.id
        });
      });
    }

    return formattedIpos;
  }
};

export default getters;



