import { IpoInterface } from "../models/ipo.interface";

/**
 * Convert the IPO data gotten from the API into a suitable format for the frontend application
 */

export const TransformApiIpoData = (response): Array<IpoInterface> => {
  const apiIpos = response.data.data[0].company_ipo;
  let ipos: IpoInterface[];

  ipos = apiIpos.map(apiIpo => {
    let ipo = {} as any;
    ipo.price_guidance = {} as any;

    ipo.id = apiIpo.id;
    ipo.issuing_house_id = apiIpo.issuing_house_id;
    ipo.registrar_id = apiIpo.registrar_id;
    ipo.issuer_name = apiIpo.issuer;
    ipo.type = apiIpo.ipo_type_id;
    ipo.price = apiIpo.price; // TO GET
    ipo.price_guidance.max = apiIpo.max_price;
    ipo.price_guidance.min = apiIpo.min_price;
    ipo.primary_units_on_offer = apiIpo.primary;
    ipo.secondary_units_on_offer = apiIpo.secondary;
    ipo.total_units_on_offer =
      parseFloat(apiIpo.primary) + parseFloat(apiIpo.secondary);
    ipo.allow_over_allotment = apiIpo.over_allotment ? true : false;
    ipo.units_for_over_allotment = apiIpo.over_allotment;
    ipo.open_date = apiIpo.open_date;
    ipo.close_date = apiIpo.close_date;
    ipo.status = apiIpo.publish_status;

    //  use_default_party_types: boolean;
    //  participants: Array<IParticipant>;
    //  participant_types: Array<any>;

    return ipo;
  });

  return ipos;
};



